@import '../../modules';

// * mypage공통
.mypage{
  background-color: $bg_main-F8F9FA;
  padding: 5.6rem 0;
  .width1352{
    width: 1352px;
    margin: 0 auto;
  }
  .board-wrap{
    background-color: white;
  }
  ul.switch{
    display: flex;
    span.bar{
      vertical-align: text-top;
      margin: 0 3rem;
      width: 1px;
      height: 18px;
      background-color: #B0B0B0;
    }
    li button, a{
      font-size: 2.4rem;
      color: #B0B0B0;
      i.icon{
        width: 2.4rem;
        height: 2.4rem;
        margin-right: 1rem;
        vertical-align: -0.14em;
        background-image: none;
      }
      &:hover,
      &.focus{
        transition: color 0.3s;
        font-weight: 700;
        color: #211221;
        i.icon svg path{
          transition: fill 0.3s;
          fill: $color-0063C0_azure-emp-02;
        }
      }
    }
  }
  div.tit{
    text-align: center;
    padding-top: 4rem;
    padding-bottom: 8.2rem;
    h3{
      @include style_font(700, 3.6rem, 1, #212121);
      padding-bottom: 2.8rem;
    }
    p{
      @include style_font(400, 2.8rem, 1, #666);
    }
  }

  div.mypage-wrap{
    width: 1352px;
    margin: 0 auto;
    background-color: white;
    border-radius: 2rem;
    padding: 9.6rem 12rem;
    ul{
      display: flex;
    }
    ul.inf-list{
      padding-top: 6.5rem;
      display: flex;
      flex-direction: column;
      gap: 2rem;
      li{
        position: relative;
        width: max-content;
        height: 6.6rem;
        span{
          padding: 2rem 0;
        }
      }
      .inf-t{
        width: 24rem;
      }
    }
  }
  div.tools{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 5rem;
    button{
      margin-left: 1.4rem;
      display: inline-block;
    }
  }
  .value{
    position: relative;
    @include style_font(500, 1.6rem, 1, #91989D);
    width: 33rem;
    height: 5.8rem;
    padding: 1.6rem 2rem;
    border: none;
    background: #FBFBFB;
    outline: 1px solid #D8D8D8;
    border-radius: 1rem;
    &::placeholder{@include style_font(500, 1.6rem, 1, #91989D);}
    &:focus{
      outline: 3px solid $color-2C99FF_azure;
    }
  }
  button.btn-toggle-visib{
    position: absolute;
    right: 1.6rem;
    top: 1.6rem;
    i.icon{
      width: 2.4rem;
      height: 2.4rem;
    }
  }
}

section.section-osp9112{
  div.tools{
    div.--l{
      a:first-child{
        color: #B0B0B0;
        text-decoration: underline;
        margin-right: 1rem;
        padding-right: 1rem;
      }
    }
  }
}

section.section-osp9116{
  ul.inf-list{
    li{
      position: relative;
    }
  }

}