// 🏠️ OSP-5111

@import '../../modules';

main.main-osp5111{
  strong{
    color: $color-2C99FF_azure;
  }

  // 뒷 배경에 원 두 개
  .background-01{
    @include style_background-01(987px, -372px, -397px, 947px, -151px, -555px);
  }


  section.tmaxos-products{
    padding: 9rem 0;
    text-align: center;
    background: #F8F9FA;
    h3{
      font-weight: 700;
      font-size: 36px;
      line-height: 1.6em;
    }
    div.products{
      padding: 9rem 0;
      ul{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 4rem;
        li{
          width: 45rem; // 31%
          height: 36.2rem;
          padding: 5.1rem 3.6rem 3.6rem 3.6rem;
          background: #FFFFFF;
          box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.05);
          border-radius: 3rem;

          .btn-rounded-03{
            height: 4.7rem;
            width: 13.1rem;
            padding: 1.4rem 2.6rem;
            font-size: 1.8rem;
            font-weight: 400;
            
          }
          span.product-name{
            width: 100%;
            text-align: left;
            @include style_font(700, 3rem, 1.8em, black);
          }
          p{
            padding-top: 3.2rem;
            // padding-bottom: calc(2.8rem + 1em);
            font-size: 18px;
            text-align: left;
            height: 16rem;
            color: $font_color-2C2C2C_black;
            line-height: 1.8em;
          }
          div{
            display: flex;
            height: 5rem;
            justify-content: space-between;
            align-items: center;
          }
        }
      }
    }
  }


  // 각 제품 특징들 
  section.product{
    .width1440{
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    //imgs
    span{ 
      width: 60rem;
      height: 60rem;
      margin: 6rem 0;
      background-size: cover;
      // imgs
      &.img_feature-1{background-image: url('../../../../public/asset/images/img_product/image-_tmaxgooroom.png');}
      &.img_feature-2{background-image: url('../../../../public/asset/images/img_product/image-_tmaxgooroom_sd.png');}
      &.img_feature-3{background-image: url('../../../../public/asset/images/img_product/image-superrds.png');}
    }
    .feature-content{
      width: 56rem;
      h4{
        font-weight: 900;
        font-size: 36px;
        line-height: 1.6em;
      }
      strong{
        display: block;
        padding-bottom: 2.2rem;
      }
      p{
        font-size: 2rem;
        line-height: 1.8em;
      }
    }
  }
  // 사진이 왼쪽(하얀색 배경)
  .product.white{ 
    .feature-content{
      margin-right: 4rem;
    }
  }
  // 사진이 오른쪽(회색 배경)
  .product.gray{ 
    background-color: $bg_main-F8F9FA;
    .feature-content{
      margin-left: 4rem;
    }
  }
}