// 🏠️ OSP-7121

@import '../../modules';


main.main-osp7121{
  position: relative;
  // 공통
  section{
    padding-top: 9.6rem;
    padding-bottom: 16.7rem
  }
  // 대문 글
  hgroup{
    @include style_font(700, 3.2rem, 1, #2e2e2e);
    padding-bottom: 9.6rem;
    border-bottom: 3px solid $line_color-ECECEC_grey;
    strong{
      color: $color-2C99FF_azure;
    }
    h2{
      padding-bottom: 2rem;
    }
  }

  div.form-layout{
    padding-top: 6.4rem;
    padding-bottom: 9.4rem;
    text-align: center;
    ul.form-ul{
      text-align: left;
      display: grid;
      gap: 1.6rem;
      // 가로 폭 설정 
      li.--w20{
        width: calc(26% - 5rem);
      }
      li.--w100{
        width: calc(100% - 5rem);
      }

      > li{
        position: relative;
        margin-left: 5rem;
        // list style (파란 점)
        &::before{
          position: absolute;
          content: '';
          top: 2.8rem;
          left: -3rem;
          width: 8px;
          height: 8px;
          background-color: $color-2C99FF_azure;
          border-radius: 50%;
        }

        // focus
        button.checked{
          outline: 3px solid $color-2C99FF_azure;
        }
        // checkbox 
        button.checkbox{
          text-align: left;
          span{
            vertical-align: text-top;
            margin-left: 1rem;
          }
        }
      }

      li.invalid{
        position: relative;
        .value{
          outline: 1px solid #FF9C9C;
        }
        &::after{
          position: absolute;
          top: -1rem;
          right: 1.8rem;
          content: attr(data-message);
          font-size: 1.2rem;
          padding: 1.2rem;
          color: white;
          width: max-content;
          background-color: #e65050;
          border-radius: 3px;
        }
      }
    }
  }
  div.dropdown{
    position: absolute;
    top: calc(100% + 8px);
    width: 100%;
  }




  // 문의하기 버튼
  input[type="submit"]{
    width: 32rem; 
    height: 7.6rem;
    margin-top: 7.6rem;
    margin-bottom: 9.3rem;
    @include style_font(700, 2.4rem, 1, white);
  }
  // 문의하기 버튼 밑
  div.inquiry{
    border-top: 4px solid #ececec;
    padding-top: 4rem;
    text-align: center;
    span{
      @include style_font(400, 1.8rem, 1, #666);
    }
    span.title{
      font-weight: 700;
      padding-right: 1.4rem;
    }
    span:nth-child(2){
      padding-right: 5.2rem;
    }
  }
  // 문의하기 완료 팜업창
  section.result-bg{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($color: #000000, $alpha: 0.3);

    display: flex;
    justify-content: center;
    div.question-result{
      width: 42.4rem;
      height: 43.7rem;
      margin-top: 24rem;
      padding: 3rem;

      text-align: center;

      background-color: white;
      border-radius: 2rem;
      box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
      div{text-align: right;}
      i.icon-mail{
        margin-top: 2.2rem;
        margin-bottom: 3.5rem;
        width: 8rem;
        height: 8.4rem;
      }
      h6{
        margin-bottom: 3.2rem;
        @include style_font(700, 2rem, 1, #3c3c3c);
      }
      p{
        margin-bottom: 6.6rem;
        @include style_font(400, 1.6rem, 1.6em, #666);
        strong{
          color: $color-2C99FF_azure;
        }
      }
      button{
        font-weight: 700;
        color: $color-2C99FF_azure;
        &.confirm{
          margin-right: 0.4rem;
        }
      }
    }
  }
  section.result-bg.in-active{
    display: none;
  }
  section.result-bg.active{
    display: flex;
  }
}