@import 'scss-reset/_reset.scss';

@import 'modules'; // *modules: 변수, mixin 패키지


// ? 뭔가 안되네용 ㅠㅠ 😥 
// $font-stack: NotoSansCJKkr-Regular, sans-serif;
// @include font-face('NotoSansCJKkr-Regular', 'NotoSansCJKkr-Regular', 400);


// 스크롤 바 숨김
::-webkit-scrollbar{
  width: 0;
  height: 0;
}

::-webkit-resizer{
  display: block;
  // width: 10000px;
  position: absolute;
  // left: 50px;
  // background-color: red;
}

html{
  font-size: 10px; // rem 기준
  scroll-behavior: smooth;
}

// * normalize
body{
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 400;
  font-size: 18px;
  font-style: normal;
  // font: 100% $font-stack;
  letter-spacing: -0.5px;

  color: #2E2E2E;

  line-height: 1;

  word-break: keep-all;
  
  a{
    text-decoration: none;
    outline: none;
    color:inherit;
    &:hover, &:active{
      text-decoration: none; 
      background-color:transparent;
    }
  }
  span, i{
    display: inline-block;
  }
  input{
    border: 0;
    
  }
  textarea{
    resize: none;
  }
}

// *🍚️ theme
.App{
  position: relative;
  min-width: 1440px;

  .width1440{ 
    width: $width_1440;
    margin: 0 auto;
  }    
  .width1352{
    width: 135.2rem;
    margin: 0 auto;
  }

  // * global 지수빈
  .clear::after{
    clear: both;
    display: block;
    content: '';
  }
  .wrap-img{
    img{
      display: block;
      width: 100%;
      min-height: 100%;
    }
  }
  .date{
    color: #B0B0B0;
    font-size: 15px;
  }

  // * global 정수진C 
  // visible
  .--h{
    visibility: hidden;
  }
  // none
  .--n{
    display: none;
  }


  // common-rule - checkbox
  input[type="checkbox"]{ 
    appearance: none;
    width: 2.4rem;
    height: 2.4rem;
    border: 1px solid #D8D8D8;
    border-radius: 3px;
    background-color: white;
    &:checked{
      transition: all 0.3s;
      background-color: $color-2C99FF_azure;
      border: 1px solid $color-2C99FF_azure;
      background-image: url('../../public/asset/images/icon/ico_CheckBox.svg');
      background-size: cover;
      background-position: center;
    }
  }




  // * tab style1 
  div.tab-style_box1{
    display: flex;
    justify-content: center;
    align-items: center;

    padding-top: 9.6rem;
    padding-bottom: 3.2rem;
    ul{
      position: relative;
      display: flex;
      &::before{
        // 그냥 border주면 안맞음
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        border-radius: 1rem;
        border: 3px solid $line_color-ECECEC_grey;
        z-index: -9;
      }
      li{
        width: 29.3rem;
        text-align: center;
        // padding: 0 1.4rem;
        button, a{
          display: inline-block;
          padding: 2rem 8rem;
          @include style_font(700, 1.8rem, 1, $font_color-AFAFAF_grey);
        }
        &.--active{
          left: 10%;
          height: 60px;
          background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
          border-radius: 1rem;
          button, a{
            color: white;
          }
        }
      } 
    }
    .tab-focus{
      width: 33.33%;
      transform: translateX(0);
    }
  }






  // * tab style2 밑줄 있는거
  div.tab-style_box2{
    position: relative;
    &::after{
      position: absolute;
      content: '';
      bottom: 0;
      left: 0;
      width: 100%;
      height: 3px;
      background: #ececec;
    }
    span.alert-new{
      background-color: #F86848;
      border-radius: 50%;
      vertical-align: top;

      width: 2rem;
      height: 2rem;
      padding-top: 0.16em;
      margin-left: 6px;
      @include style_font(700, 1.4rem, 1, white);
    }
    ul{
      display: flex;
      justify-content: center;
      li{
        text-align: center;
      }
      a, button{
        position: relative;
        display: inline-block;
        font-weight: 700;
        font-size: 20px;
        width: 220px;
        padding: 5.2rem 1em 3.8rem 1em;
        color: $font_color-AFAFAF_grey;
        &::after{
          position: absolute;
          content: '';
          bottom: 0;
          left: 0;
          width: 100%;
          height: 5px;
          background: $color-2C99FF_azure;
          z-index: 99;
          opacity: 0;
          transition: opacity 0.3s;
        }
      }
      .tab-focused, button:hover{
        color: $color-2C99FF_azure;  
        transition: all 0.3s;
        &::after{
          opacity: 1;
        }
      }
    }
  }
  // * tab style2 - commu
  div.tab-style_box2.community{
    ul.width1440{
      a, button{
        &::after{
          background: $color-0AB4E2_commu-01;
        }
      }
      .tab-focused, button:hover{
        color: $color-0AB4E2_commu-01;  
      }
    }
  }

  // * 버튼 - basic theme
  .btn{
    display: inline-block;
    transition: all 0.3s;
  }

  .btn-basic-01{
    padding: 1.1rem 2rem;
    height: 4.2rem; 
    border-radius: 5px;
    &.--d{
      color: $font_color-B0B0B0_grey;
      border: 1px solid $font_color-B0B0B0_grey;
    }
    &.--a{
      color: #666666;
      border: 1px solid #666666;
      &:active{
        color: #212121;
        border: 1px solid #212121;
      }
    }
  }
  .btn-basic-02{
    display: inline-flex;
    padding: 1.1rem 2rem;
    height: 4.2rem; 
    border-radius: 5px;
    &.--d{
      background-color: #B0B0B0;
      color: white;
    }
    &.--a{
      color: white;
      background-color: $color-2C99FF_azure;
      &:hover{
        background-color: $color-007DF1_azure-emp-01;
      }
      &:active{
        background-color: $color-0063C0_azure-emp-02;
      }
    }

  }

  .btn-rounded-01{
    @include style_font(500, 1.8rem, 1, $color-2C99FF_azure);
    padding: 1.5rem 3rem 1rem 3rem;
    background: white;
    border: 1px solid $color-2C99FF_azure;
    border-radius: 4rem;
    .icon-down-svg svg{
      width: 2.4rem;
      height: 2.4rem;
      path{
        fill: $color-2C99FF_azure;
      }
    }
    &:hover{
      color: $color-007DF1_azure-emp-01;
      border: 1px solid $color-007DF1_azure-emp-01;
      .icon-down-svg svg path{
        fill: $color-007DF1_azure-emp-01;
      }
    }
    &:active{
      color: $color-0063C0_azure-emp-02;
      border: 1px solid $color-0063C0_azure-emp-02;
      .icon-down-svg svg path{
        fill: $color-0063C0_azure-emp-02;
      }
    }

  }
  .btn-rounded-02{
    @include style_font(700, 2.2rem, 1, $color-2C99FF_azure);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 1.5rem 3rem;

    background: white;
    border-radius: 4rem;
    transition: all 0.3s;
    color: $color-2C99FF_azure;
    &:hover{
      color: $color-007DF1_azure-emp-01;
    }
    &:active{
      color: $color-0063C0_azure-emp-02;
    }
  }


  .btn-rounded-02.commu{
    color: $color-0AB4E2_commu-01;
    &:hover{
      color: $color-06A1CB-commu-02;
    }
    &:active{
      color: $color-008CB2-commu-03;
    }
  }



  .btn-rounded-03{
    @include style_font(700, 2.2rem, 1.2, white);
    border-radius: 4rem;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    // height: 6.4rem;
    // padding: 1.65rem 3.5rem 1.5rem 5.5rem;
    background-color: $color-2C99FF_azure;
    transition: background-color 0.3s;
    i.icon{
      // width: 3.4rem;
      // height: 3.4rem;
      margin-left: 1rem;
      // vertical-align: -0.66em;
    }
    &:hover{background-color: $color-007DF1_azure-emp-01;}
    &:active{background-color: $color-0063C0_azure-emp-02;}
  }
  
  // 추후에 상태 업데이트 예정 
  .btn-rounded-03-grad{
    @include style_font(500, 1.8rem, 1.2, white);
    background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
    border-radius: 4rem;
    .icon-down-svg svg{
      width: 2.4rem;
      height: 2.4rem;
      path{
        fill: white;
      }
    }
  }
  


  // * 버튼 - 메뉴 박스 토글 (점 세 개)
  .btn-box-toggle{
    i.icon{
      width: 2.4rem;
      height: 2.4rem;
      opacity: 0.3;
      transition: opacity 0.3s;
    }
    &:hover{
      i.icon{
        opacity: 0.6;
      }
    }
    &:active{
      i.icon{
        opacity: 1;
      }
    }
  }  
  // 도구 박스 열리면 나오는 거 (수정, 삭제) 
  ul.open-box{
    position: absolute;
    top: calc(2.2rem + 1rem);
    right: 0rem;

    padding: 6px;

    background-color: white;
    box-shadow: 0px 8px 20px rgba(15, 23, 42, 0.06);
    border: 1px solid #D8D8D8;
    border-radius: 1rem;
    li{
      margin-bottom: 4px;
      position: relative;
    }
    button{
      width: 14.8rem;
      height: 4.4rem;
      padding-left: 1rem;
      text-align: left;
      @include style_font(400, 1.6rem, 1, #91989D);
      &:hover{
        color: $color-0AB4E2_commu-01;
      }
    }
  }

  // * 버튼 - 더보기나 상세페이지 이동
  .btn-more{ 
    font-weight: 500;
    padding: 1rem 0;
    color: $color-0082F3_btn;
    text-align: center;
    i.icon{
      margin-left: 6px;
    }
    &:hover{color: $color-007DF1_azure-emp-01;}
    &:active{color: $color-0063C0_azure-emp-02;}
  }

  .btn-more.commu{ 
    @include style_font(500, 1.8rem, 1, $color-008CB2-commu-03);
    display: inline-block;
    color: $color-0AB4E2_commu-01;
    &:hover{color: $color-06A1CB-commu-02;}
    &:active{color: $color-008CB2-commu-03;}
  }

  // * 버튼 - commu theme

  .btn-basic-02.commu{
    &.--a{
      background-color: $color-0AB4E2_commu-01;
      &:hover{
        background-color: $color-06A1CB-commu-02;
      }
      &:active{
        background-color: $color-008CB2-commu-03;
      }
    }

  }
  .btn-rounded-01.commu{
    @include style_font(500, 1.8rem, 1, $color-0AB4E2_commu-01);
    background: #FFFFFF;
    border: 1px solid $color-0AB4E2_commu-01;
    border-radius: 4rem;
    .icon-down-svg{
      vertical-align: middle;
      width: 2.4rem;
      height: 2.4rem;
      margin-left: 1rem;
      svg path{
        fill: $color-0AB4E2_commu-01;
      }
    }
    &:hover{
      color: $color-06A1CB-commu-02;
      border: 1px solid $color-06A1CB-commu-02;
      i.icon svg path{
        fill: $color-06A1CB-commu-02;
      }
    }
    &:active{
      color: $color-008CB2-commu-03;
      border: 1px solid $color-008CB2-commu-03;
      i.icon svg path{
        fill: $color-008CB2-commu-03;
      }
    }
  }


  .btn-rounded-03.commu{
    @include style_font(500, 1.8rem, 1, white);
    padding: 1.5rem 3rem;
    background-color: $color-0AB4E2_commu-01;
    &:hover{
      background: #06A1CB;
    }
    &:active{
      background: $color-008CB2-commu-03;
    }
  }

  // * 버튼 - 목록보기
  .btn-link-list{
    display: inline-flex;
    align-items: center;
    height: 5.8rem;
    padding: 1.4rem 4.4rem;
    // margin-top: 4.4rem;
    border-radius: 4rem;

    color: #666666;
    border: 1px solid #B0B0B0;

    background-color: white;
    transition: all 0.2s;
    i.icon{
      margin-right: 1rem;
    }

    // 동작정의
    &:hover{
      color: #3C3C3C;
      border: 1px solid #666666;
    }

    &:active{
      color: #212121;
      border: 1px solid #3C3C3C;
    }
  }


