// 🏠️ OSP-3111
@import '../../modules';

// 메인화면 배너 
section.banner-wrapper{
  position: relative;
  height: 89.6rem;

  overflow: hidden;

  display: flex;
  width: 200vw;
  
  // 슬라이드_
  .banner-container{
    // transform: translateX(-100%);
    // transform: translateX(0%);

    // width: 1920px;
    // width: 50%; 
    // width: 1920px;
    width: 100vw;
    height: 100%;
    background-position: left;
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
    
    // 슬라이드_내용
    .slider-content{
      position: relative;
      height: 100%;


      padding-left: 5.4rem;
      color: $font_color-white;
      h1{
        padding-top: calc(19.2rem + 8.4rem);
        @include style_font(700, 5.2rem, 1.4em, $font_color-white);
        margin-bottom: 1.6rem;
      }
      p{
        font-weight: 300;
        line-height: 1.8em;
        padding-top: 0.4rem;
        padding-bottom: 5.2rem;
      }

      div.slider-controller{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 13rem;
        background-color: rgba(0, 0, 0, 0.08);
        .page-now{
          font-weight: 700;
        }
        .page-total{
          color: rgba(255, 255, 255, 0.4);
        }
        button i{
          width: 3rem;
          height: 3rem;
        }
      }
    }
  }

  // 슬라이드 배경
  .banner-container.slide-01{
    background-image: url('../../../../public/asset/images/main_banner.png');
  }
  .banner-container.slide-02{
    background-color: gainsboro;
  }
}

// ---------------------------------
main.main-osp3111{
  position: relative;

  // 메인 화면 공지사항
  .banner-notice{
    position: absolute;
    top: -73px;
    width: 100%;
    z-index: 999;
    .width1440{
      display: flex;
      align-items: center;
      height: 73px;
      padding: 1em 20px;
      background-color: rgba(0, 10, 47, 0.44);
      border-radius: 1em 1em 0 0;
    }
    span{
      width: 108px;
      color: $font_color-white;
      padding: 5px 20px;
      font-weight: 700;
    }
    a{
      color: $font_color-white;
    }
    .notice-now{
      width: calc(100% - 258px);
      padding: 1em;
      font-weight: 300;
    }
    .notice-all{
      padding: 1em;
      width: 150px;
      text-align: right;
      font-weight: 500;
      .icon-more_arrow{
        margin-left: 4px;
        background-image: none;
      }
    }
  }

  section.main-product{

    @include style_main_h3($font_color-white);
    
    position: relative;
    text-align: center;

    // 뒷 배경 (원) 설정
    background: linear-gradient(90.72deg, #4661FF 0.62%, #279FFF 99.38%);
    z-index: 0;
    overflow: hidden;
    &::before{
      position: absolute;
      content: '';
      z-index: -1;
      background-repeat: no-repeat;
      background-image: url('../../../../public/asset/images/res/bg_circle_1.svg');
      background-size: 2020px;
      width: 2020px;
      height: 2020px;
      top: -868px;
      left: -686px;
      opacity: 0.1;
    }
    &::after{
      position: absolute;
      content: '';
      z-index: -1;
      background-repeat: no-repeat;
      background-image: url('../../../../public/asset/images/res/bg_circle_1.svg');
      // mix-blend-mode: multiply;
      background-size: 1693px;
      width: 1693px;
      height: 1693px;
      top: 261px;
      right: -841px;
      opacity: 0.08;
    }

    .width1440{
      h3{
        padding-bottom: 9.6rem;
      }
      padding: 9.6rem 0;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
    }

    // 제품 버튼들
    div.product-cards{
      width: 100%;
      display: flex;
      justify-content: space-between;
      button.btn-product{
        width: 462px;
        height: 645px;
        padding-top: 6px;
        padding-bottom: 57px;

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;

        background-color: rgba($color: white, $alpha: 1);

        box-shadow: 0px 20px 20px rgba(0, 0, 0, 0.08);
        border-radius: 3rem;
        
        // 이미지
        span.img{
          display: block;
          width: 34rem;
          height: 34rem;

          &.img_feature-1{background-image: url('../../../../public/asset/images/img_product/image-_tmaxgooroom.png');}
          &.img_feature-2{background-image: url('../../../../public/asset/images/img_product/image-_tmaxgooroom_sd.png');}
          &.img_feature-3{background-image: url('../../../../public/asset/images/img_product/image-superrds.png');}
        }

        // 제품명
        h4{
          font-size: 32px;
          font-weight: 700;
          margin: 34px 0 36px 0;
        }

        // 제품 내용
        p{
          width: 382px;
          height: 81px;
          font-size: 19px;
          line-height: 150%;
          color: $font_color-848484_grey;
        }
        // 자세히 보기 
        span{
          margin-top: 33px;
          i{
            margin-left: 6px;
          }
        }
      }

      // 동작 정의 
      button.btn-product{
        // hover 애니메이션
        transition: transform 0.3s;
        span.img{
          transition: transform 0.3s;
        }
        &:hover{
          transform: translateY(-4rem);

          // 이미지 커짐
          span.img{
            transform: scale(1.125);
          }

          // 화살표 간격 넓어짐
          span.btn-more i.icon{
            margin-left: 8px;
          }
        }

        // press 애니메이션
        &:active{
          box-shadow: none;
          background-color: rgba($color: white, $alpha: 0.9);
        }
      }
    }
  }

  section.main-customer{
    background-color: $bg_color-F8F8F8-grey;
  }
}
