@import '../modules';

// ! animation 추후에 정하기 
div.tab-boxed-fix{
  // position: fixed;
  // width: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
  margin: 9.6rem 0;
  ul.tab-list{
    position: relative;
    display: flex;
    background-color: white;

    // 그냥 border주면 안맞음
    &::before{
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;

      border-radius: 1rem;
      border: 3px solid $line_color-ECECEC_grey;
    }

    li{
      width: 29.3rem;
      text-align: center;
      z-index: 9;

      a{
        display: inline-block;
        padding: 2rem 8rem;
        @include style_font(700, 1.8rem, 1, $font_color-AFAFAF_grey);
      }
    }
    li.--focused{
      left: 10%;
      height: 60px;
      background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
      border-radius: 1rem;
      a{
        color: white;
      }
    }
  }
}