// 작성자: 정수진C
@import '../modules';

section.main-board{
  .width1440{
    text-align: center;
    padding-top: 9.5rem;
  }
  @include style_main_h3($font_color-black);
  div.board-cards{
    margin-top: 7.6rem;
    display: flex;
    height: 289px;
    justify-content: space-between;
    button{
      position: relative;
      width: 335px;

      display: flex;
      flex-direction: column;
      justify-content: space-between;

      text-align: left;
      img{
        width: 100%;
        height: 195px;
        border-radius: 1rem;

        background-color: dimgray;
      }
      .board-title{
        font-size: 20px;
        font-weight: 500;
        line-height: 150%;
        color: #000000;
      }
    }
  }
  a.btn-more{
    margin: 10rem 0;
  }
}
// 뉴스룸
section.main-news{
  @include style_main_h3($font_color-black);
  padding-top: 9.6rem;
  background: $bg_main-news;
  text-align: center;
  div.news-grid{
    margin-top: 7.6rem;
    position: relative;
    height: 683px;

    display: grid;
    grid-template-columns: 830px 577px;

    column-gap: 3.2rem;
    row-gap: 3rem;
    button{
      text-align: left;
      border-radius: 10px;
      background-color: white;
      .news-content{
        padding: 4rem;
        .news-title{
          height: 77px;
          font-weight: 500;
          font-size: 24px;
          line-height: 150%;
          color: $font_color-black;
          margin-bottom: 20px;
          // 두 줄부터 ... 해야해용
          // white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
        .news-date{
          line-height: 170%;
          color: $font_color-B0B0B0_grey;
          margin-top: 14px;
          font-weight: 300;
        }        
      }
    }
    // 첫번째 레이아웃
    .--col1{
      display: flex;
      flex-direction: column;
      justify-content: space-between;        
      height: 100%;
      grid-row-start: 1;
      grid-row-end: 4;
      img{
        width: 830px;
        height: 439px;
        border-radius: 10px;
        background-color: darkgray;
      }
      .news-content{
        position: relative;
        height: 244px;
        padding: 4rem;
        p{
          line-height: 170%;
          width: 100%;
          letter-spacing: -0.5px;
        }
        .news-title{
          height: 55px;
          font-weight: 700;
          font-size: 26px;
          color: $font_color-black;
          margin-bottom: 5px;
        }
      }
    }
  }
  a.btn{
    margin: 10rem 0;
  }
}
// 배너 - 고객문의
section.main-customer{
  .width1440{
    position: relative;
    height: 35.7rem;
  }
  .customer-banner{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    padding: 7rem 16rem;
    .customer-title{
      padding: 1em 8.6rem;
      color: $font_color-white;
      h5{
        text-align: left;
        font-weight: 700;
        font-size: 36px;
        padding-bottom: 2.4rem;
      }
      p{
        font-weight: 400;
        font-size: 24px;
      }
    }
    // 버튼
    button.btn{
      text-align: center;
      width: 280px;
      height: 76px;

      background: #FFFFFF;
      border-radius: 48px;
      font-size: 24px;
    }
  }
  .--banner{
    position: absolute;
    bottom: 0;      
    
    width: 100%;
    height: 299px;
    background: linear-gradient(90.72deg, #4661FF 0.62%, #4BA5FF 99.38%);
    border-radius: 30px;
  }
}

