// 🏠️ OSP-4112

@import '../../modules';


main.main-osp4112{
  .width1352{
    width: 1352px;
    margin: 18rem auto;
  }
  section.width1352{
    display: flex;
    padding: 5.8rem 7.5rem 13rem 8.7rem;
    box-shadow: 0px 30px 50px rgba(175, 175, 175, 0.1);
    border-radius: 50px;
    div{
      flex: 1 1 auto;
    }
    hgroup.img_tamxos_ceo{
      display: block;
      width: calc(428px + 5.5rem);
      padding-top: 11rem;
      height: 467px;
      // background-size: 428px 100%;
      background: url('../../../../public/asset/images/img-OSP4112-ceo.png') right no-repeat;
      h3{
        font-weight: 700;
        font-size: 36px;
        padding-bottom: 2rem;
        // 파란 점 
        position: relative;
        &::before{
          position: absolute;
          content: '';
          top: -4rem;
          left: -4rem;
          width: 3rem;
          height: 3rem;
          background-image: url('../../../../public/asset/images/icon/ico_subtract.svg');
        }
      }h4{
        font-weight: 700;
        font-size: 18px;
      }
    }
    div.ceo-right{ 
      position: relative;
      margin-top: 2.5rem;
      margin-left: 8.4rem;
      &::after,
      &::before{
        position: absolute;
        content: '';
        width: 142px;
        height: 105px;

        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        background-image: url('../../../../public/asset/images/icon/ico_quotes.svg');
      }
      &::after{
        bottom: -2rem;
        right: -4rem;
        transform: rotate(180deg);
      }
      &::before{
        top: -7rem;
        left: -12rem;
        // background-blend-mode: multiply;
        mix-blend-mode: multiply;
      }
      p{
        line-height: 1.6em;
        font-weight: 300;
        font-size: 18px;
        color: #212121;
        padding-bottom: 1.6em;
      }

    }
  }

}