// 🏠️ OSP-4113

@import '../../modules';

main.main-osp4113{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 18rem;
  // 타이틀 - 조직
  section.title{
    padding-top: 123px;
    margin-bottom: 18rem;
    display: flex;
    // 타이틀_내용
    div.title-cont{
      padding-top: 14.7rem;
      h3{
        font-weight: 900;
        font-size: 32px;
        color: $color-2C99FF_azure;
        strong{
          font-size: 72px;
        }
      }
      h2{
        padding-top: 1.6rem;
        padding-bottom: 2.8rem;
        padding-right: 23.8rem;
        font-weight: 700;
        font-size: 40px;
        line-height: 1.6em;
      }
      p{
        font-size: 2.4rem;
      }
    }
    // 이미지_타이틀
    span.img_tmaxos-1{
      // 회색 원
      &::before{
        position: absolute;
        content: '';
        top: 0;
        left: -65%;
        width: 100%;
        height: 100%;
        background-color: $bg_main-news;
        border-radius: 50%;
        z-index: -9;
      }
      position: relative;
      display: block;
      width: 683px;
      height: 683px;
      border-radius: 50%;
      background-image: url('../../../../public/asset/images/img-OSP4113.png');
      background-size: cover;
    }
  }
  // 이미지 - 조직도
  div.svg_tmaxos-group{
    width: 1190px;
    height: 948px;
    background-image: url('../../../../public/asset/images/svg_tmaxos_gr.svg');
    background-size: cover;
    background-position: center;
  }
}