@import './../modules';

footer{
  background-color: $bg-main-news;
  color: $font_color-737373_grey;
  // 사이트-맵
  div.footer-sitemap{
    border-bottom: 1px solid $line_color-E7E7E7_grey;
    // depth1
    ul.site-map-depth1{
      display: flex;
      padding: 5.6rem 0;
      li{
        font-size: 14px;
        width: 25rem;
        h4{
          padding-bottom: 2rem;
          font-weight: 700;
        }
      }
      // 나머지 사이트
      li.another-site{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        a{
          @include style_font(700, 1.4rem, 1, #737373);
          i.icon{
            background-image: none;
          }
        }
        // 인재 채용 
        a.link-to-career{
          i.icon{
            vertical-align: -0.2em;
          }
        }
        a.link-to-another{
          i.icon{
            width: 1.8rem;
            height: 1.8rem;
            margin-left: 2px;
          }
        }
      }
    }
    // depth2
    ul.site-map-depth2{
      li a{
        display: inline-block;
        padding: 1rem 0;
      }
    } 
  }
  // 사이트-정보
  div.footer-information{
    display: flex;
    justify-content: space-between;
    padding: 4rem 0;
    // 로고
    div.logo-wrapper{
      a.logo{
        width: 12rem;
        height: 2.8rem;
      }
    }
    // 회사 정보 및 방침
    div.infors{
      width: 75%;
      font-size: 1.4rem;
      ul{
        margin-bottom: 1.6em;
        li{
          display: inline-block;
          font-weight: 700;
          margin-right: 3.2em;
        }
      }
      address, small{
        font-size: inherit;
        padding-bottom: 1.2rem;
        line-height: 1.5em;
      }
    }
    // 인증마크 확대 버튼
    button.btn-certificate{
      align-self: flex-end;
      position: relative;
      width: 9rem;
      height: 6rem;
      img{
        width: 100%;
        height: 100%;
      }
    }
  }
}
