// 🏠️ OSP-4117

@import '../../modules';

main.main-osp4117{
  div.width1440{
    padding: 9.6rem 0;
    address{
      // 주소
      span.addr-map{
        @include style_font(500, 2.4rem, 1, #212121);

        position: relative;
        padding-bottom: 4rem;
        padding-left: 4rem;
        // 파란 점
        &::before{
          position: absolute;
          content: '';
          top: 0rem;
          left: 0rem;
          width: 2rem;
          height: 2rem;
          background-image: url('../../../../public/asset/images/icon/ico_subtract.svg');
          background-size: 2rem 2rem;
        }
      }
      >p{
        padding: 0 0 2.2rem 4rem;
        @include style_font(400, 2rem, 1, #666666);
        span{
          width: 7.6rem;
          @include style_font(700, 2rem, 1, #666666);
        }
      }
    }

    iframe{
      margin-top: 1.8rem;
      width: 100%;
      height: 67.1rem;
    }
  }
}